import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";
import { toast } from "./Toast";
import { errorMessage } from "../libs/Misc";
import { TabContainer, TabBodyScrollable, TabTitle, TabParagraph, TabNextButton } from "./TabsComponents";
import { ServiceContext } from "../providers/ServiceProvider";
import JobService from "../services/JobService";

function Tab05Check(props) { // TODO: we need file here...
  const { t } = useTranslation();
  const { service, setService } = useContext(ServiceContext);
  const [ statusLocal, setStatusLocal ] = useState({});
  const [ nextIsEnabled, setNextIsEnabled ] = useState(false);
  
  useEffect(() => {
console.log("Tab05Check 1 props, service:", props, service);
    if (props.active) {
console.log("Tab05Check 1 props active");
      if (service.file && !service.transform) {
console.log("Tab05Check 1 file && !transform: calling transform");
        (async () => {
          setStatusLocal({loading: true});
          await JobService.transformXls2Xml(service.file.path).then(
            async response => {
console.log("transform response:", response);
              if (!response) { // TODO: response.ok ? ...
                //console.warn("transformXls2Xml error:", JSON.stringify(data));
                // TODO: ok?
                toast.error(errorMessage(response.message));
                setStatusLocal({error: response.message});
                return;
              }
console.log("transform success:", response.data);
              setService({...service, transform: response.data.result});
              setStatusLocal({success: response.data});
              //setNextIsEnabled(true);
            },
            error => {
              console.error('transformXls2Xml error:', error);
              setService({...service, transform: error}); // to stop repeated calls...
              toast.error(errorMessage(error));
            }
          );
        })();
      }
    }
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [props, service/*, setService*/]);

  useEffect(() => {
console.log("Tab05Check 2 props, service:", props, service);
    if (props.active) {
console.log("Tab05Check 2 props active");
      if (service.file && service.transform && !service.validateXml) {
console.log("Tab05Check 2 file && transform !validate: calling validate");
        (async () => {
          setStatusLocal({loading: true});
          await JobService.validateXml(service.transform).then(
            response => {
console.log("validate response:", response);
              if (!response) { // TODO: response.ok ? ...
                //console.warn("transformXls2Xml error:", JSON.stringify(data));
                // TODO: ok?
setService({...service, validateXml: "no response"});
                toast.error(errorMessage(response.message));
                setStatusLocal({error: response.message});
                return;
              }
console.log("validate success:", response.data);
              setService({...service, validateXml: response.data.result});
              setStatusLocal({success: response.data});
              setNextIsEnabled(true);
            },
            error => {
              setService({...service, validateXml: error}); // to stop repeated calls...
              // console.error('validateXml errors:', error, error.message, error.status, Object.keys(error), Object.values(error));
              // console.error('validateXml error:', error);
              // console.error('validateXml error.message:', error.message);
              // console.error('validateXml error.request:', error.request);
              // console.error('validateXml error.request.status:', error.request.status);
              // console.error('validateXml error.request.statusText:', error.request.statusText);
              // console.error('validateXml error keys:', Object.keys(error));
              // console.error('validateXml error values:', Object.values(error));
              toast.error(errorMessage(error));
            }
          );
        })();
      }
    }
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [props, service/*, setService*/]);

  //             await JobService.validateXml(service.transform).then(
  //               response => {
  //   console.log("validateXml XXX:", response);
  //                 if (!response) { // TODO: response.ok ? ...
  //                   //console.warn("transformXls2Xml error:", JSON.stringify(data));
  //                   // TODO: ok?
  //                   toast.error(errorMessage(response.message));
  //                   setStatusLocal({error: response.message});
  //                   return;
  //                 }
  //                 console.log("validateXml success:", response.data);
  //                 setService({...service, validateXml: response.data.result});
  //                 setStatusLocal({success: response.data});
  //                 setNextIsEnabled(true);
  //               },
  //               error => {
  //   setService({...service, validateXml: error}); // TO AVOID LOOPS... (TODO: use better logic...)
  //                 console.error('validateXml error:', error);
  //                 toast.error(errorMessage(error));
  //               }
  //             );





  const onNext = () => {
    props.goto("next");
  };

  return (
    <TabContainer>
      <TabBodyScrollable>
        <TabTitle>
          {t("Check")}
        </TabTitle>
        <TabParagraph>
         {/* SL:  {JSON.stringify(status.service?.transform)} */}
          {statusLocal && "loading" in statusLocal && `Elaborazione in corso...`}
          {statusLocal && "error" in statusLocal && `Errore: ${statusLocal.error}`}
          {statusLocal && "success" in statusLocal && `Elaborazione completata`}
        </TabParagraph>
        {statusLocal && "error" in statusLocal && `Errore: ${statusLocal.error}`}
        {((statusLocal && "success" in statusLocal) || (statusLocal && "error" in statusLocal)) && (
          <pre>
            {
              JSON.stringify(service, null, 2)
            }
          </pre>
        )}
      </TabBodyScrollable>

      <TabNextButton onNext={onNext} nextIsEnabled={nextIsEnabled}>
        {`${t("Continue")}`}
      </TabNextButton>
    </TabContainer>
  );
}
Tab05Check.propTypes = {
  goto: PropTypes.func.isRequired,
};
Tab05Check.defaultProps = {
};

export default React.memo(Tab05Check);